.page-header {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  margin-bottom: -66px;
  background-color: rgba(0, 0, 0, 0.3);
}
.page-header--opened,
.page-header--nojs {
  background-color: #283645;
}
.page-header__wrapper {
  width: 100%;
}
.page-header__top {
  height: 66px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.page-header__logo {
  width: 75px;
  height: 22px;
  display: inline-block;
  fill: #ffffff;
}
.page-header__logo:hover {
  fill: #d22856;
}
.page-header__logo:active {
  fill: #ffffff;
  opacity: 0.3;
}
.page-header__toggle {
  position: relative;
  display: block;
  width: 50px;
  height: 24px;
  padding: 0;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  border: none;
}
.page-header--closed .page-header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.page-header--closed .page-header__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.page-header--closed .page-header__toggle:active::before {
  background-color: #ac4e67;
  box-shadow: 0 10px 0 0 #ac4e67, 0 20px 0 0 #ac4e67;
}
.page-header--opened .page-header__toggle::before,
.page-header--opened .page-header__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  right: -4px;
  width: 31px;
  height: 4px;
  background-color: #ffffff;
}
.page-header--opened .page-header__toggle::before {
  transform: rotate(45deg);
}
.page-header--opened .page-header__toggle::after {
  transform: rotate(-45deg);
}
.page-header--opened .page-header__toggle:hover::before,
.page-header--opened .page-header__toggle:hover::after {
  background-color: #d22856;
}
.page-header--opened .page-header__toggle:active::before,
.page-header--opened .page-header__toggle:active::after {
  background-color: #ac4e67;
}
.page-header--opened .page-header__top {
  height: 70px;
}
@media (max-width: 959px) {
  .page-header--nojs .page-header__toggle {
    display: none;
  }
}
@media (min-width: 660px) {
  .page-header {
    margin-bottom: -110px;
  }
  .page-header__top {
    height: 110px;
  }
  .page-header__logo {
    width: 147px;
    height: 40px;
    margin-bottom: 18px;
  }
  .page-header__logo-image {
    width: 147px;
    height: 40px;
  }
  .page-header--opened .page-header__top {
    height: 110px;
  }
}
@media (min-width: 960px) {
  .page-header__toggle {
    display: none;
  }
  .page-header__top {
    display: inline-block;
    height: auto;
    padding: 20px 0 0;
  }
  .page-header__wrapper {
    display: flex;
    max-width: 940px;
    width: 100%;
  }
  .page-header {
    flex-direction: row;
    justify-content: space-around;
    min-height: 110px;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .page-header__logo {
    flex-shrink: 0;
    margin-right: 136px;
    margin-bottom: 0;
    margin-top: 7px;
  }
}
.main-nav {
  position: relative;
  width: 100%;
  margin: 0 auto;
  background-color: #283645;
}
.page-header--closed .main-nav {
  display: none;
}
.page-header--nojs .main-nav {
  display: block;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
}
.main-nav__item {
  width: 100%;
  height: 64px;
}
.main-nav__item .site-link {
  display: block;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 18px;
  font-weight: bold;
  line-height: 32px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #283645;
  box-shadow: 0px 1px 0 0 #000000, inset 0px 1px 0 0 rgba(255, 255, 255, 0.15);
}
.main-nav__item .site-link[href]:hover {
  color: #d22856;
}
.main-nav__item .site-link[href]:active {
  color: #ac4e67;
}
@media (max-width: 959px) {
  .page-header--nojs .main-nav__list {
    display: flex;
  }
}
@media (min-width: 960px) {
  .main-nav {
    min-height: 110px;
    background-color: rgba(0, 0, 0, 0);
  }
  .page-header--closed .main-nav {
    display: block;
  }
  .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .main-nav__item {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 35px;
    min-height: 110px;
    width: auto;
  }
  .main-nav__item:last-child {
    padding-right: 0;
  }
  .main-nav__list .site-link {
    padding: 10px 0;
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    color: #ffffff;
    text-transform: none;
    background-color: rgba(0, 0, 0, 0);
    box-shadow: none;
  }
  .main-nav__item .site-link[href]:hover {
    color: #d22856;
  }
  .main-nav__item .site-link[href]:active {
    color: rgba(255, 255, 255, 0.3);
  }
  .main-nav__link--active {
    border-bottom: 2px solid #ffffff;
  }
}
