  overflow-x: hidden;
  background-color: #f2f2f2;
  position: relative;
}
.rates::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-right: 100vw solid #f2f2f2;
  border-top: 25px solid #ffffff;
