.rates__data--no::before {
  background-image: url(../img/no.svg);
  background-size: 28px 28px;
}
.rates__data--empty {
  border: none;
}
.rates__title {
  width: 280px;
  padding: 15px 0 19px 0;
  position: relative;
  z-index: -1;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.67;
  text-align: center;
