  margin-top: 0;
}
.popup__text {
  font-size: 14px;
  color: #283645;
}
.popup__button-wrapper {
  padding: 48px 20px 52px;
  background-color: #f2f2f2;
}
.popup__button {
  background-color: #2aab6d;
  border: 2px solid #2aab6d;
  border-radius: 2px;
  width: 276px;
  height: 59px;
  font-size: 18px;
  font-weight: bold;
  line-height: 30px;
  color: #ffffff;
  text-transform: uppercase;
}
.popup__button-wrapper--wrong {
  padding-top: 0;
  background-color: #ffffff;
}
@media (min-width: 660px) {
  .popup {
    width: 460px;
  }
  .popup__header {
    font-size: 30px;
    line-height: 40px;
  }
  .popup__text {
    font-size: 18px;
  }
  .popup__wrapper {
    padding: 35px 80px 20px;
  }
  .popup__button {
    width: 296px;
  }
}
@media (min-width: 960px) {
